/**
 * @Description:
 */
import { App } from 'vue'

function Debounce (app:App, options: any) {
  app.directive('debounce', {
    beforeMount: (el, binding) => {
      let { fn, delay } = binding.value
      let timer:any = null
      
      el.addEventListener('keyup', function() { // keyup
        
        if (timer) { clearTimeout(timer) } // timer
        timer = setTimeout(() => {
          fn.apply(this) // 
          timer = null
        }, delay || 500)
      })
    }
  })
}

export default Debounce